import React from 'react';
import btnLeftArrow from './icons/left-arrow.svg';
import btnRightArrow from './icons/right-arrow.svg';

const BtnSlider = ({moveSlide, direction}) => {
    return (
        <button 
        onClick={moveSlide}
        className={direction === 'next' ? 'btn-slide next' : 'btn-slide prev'}>
            <img src={direction === 'next' ? btnRightArrow : btnLeftArrow} alt="icones flÃ¨ches" />
        </button>
    );
};

export default BtnSlider;